<template>
    <div id="addTest">
        <p class="title_main"></p>
        <div class="view_main">
            <span></span>
            <el-select v-model="isUse" placeholder="">
                <el-option
                v-for="item in isUseMenu"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <span></span>
            <el-select v-model="isUse" placeholder="">
                <el-option
                v-for="item in isUseMenu"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <span></span>
            <el-select v-model="isUse" placeholder="">
                <el-option
                v-for="item in isUseMenu"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div class="view_main">
            <span></span>
            <el-input class="input_test" placeholder=""></el-input>
        </div>
        <div class="view_main">
            <span></span>
            <el-upload
            class="upload-demo"
            action="https://jsonplaceholder.typicode.com/posts/"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
            multiple
            :limit="3"
            :on-exceed="handleExceed"
            :file-list="fileList">
                <el-button size="small" type="primary"></el-button>
              <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div>-->
            </el-upload>
            <span></span>
            <el-upload
            class="upload-demo"
            action="https://jsonplaceholder.typicode.com/posts/"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
            multiple
            :limit="3"
            :on-exceed="handleExceed"
            :file-list="fileList">
                <el-button size="small" type="primary"></el-button>
                <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div>-->
            </el-upload>
        </div>
        <div  class="view_main">
            <ul>
                <li>
                    <span></span>
                    <el-input class="input_type"></el-input>
                </li>
            </ul>
            <el-button></el-button>
        </div>
        <div  class="view_main">
            <span></span>
            <el-select v-model="isUse" placeholder="">
                <el-option
                v-for="item in isUseMenu"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div  class="view_main">
            <span></span>
            <el-input class="input_type"></el-input>
            <span></span>
        </div>
        <div  class="view_main">
            <el-button></el-button>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return{
                isUseMenu: [
                    {value: "true",
                        label: ""},
                    {value: "false",
                        label: ""}
                ],
                isUse: "false",
                fileList: []
            }
        },
    methods:{
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${ file.name }`);
        }
    }
    }
</script>
<style>
.title_main{
    padding:20px 0;
    border-bottom: 1px solid #999;
    }
.view_main{
    overflow:hidden;
    margin-top:10px
    }
.input_test{
    width: 500px;
    }
.input_type{
    width: 200px;
    }
</style>


